import React from 'react'
import { Redirect, Route, Switch } from 'react-router-dom'
import { Container } from '../components/navbar/container'
import { HomeScreen } from '../components/Screens/HomeScreen'
import { PhotosScreen } from '../components/Screens/PhotosScreen'
import { StoreScreen } from '../components/Screens/StoreScreen'
import { TornamentScreen } from '../components/Screens/TornamentScreen'
import { UserScreen } from '../components/Screens/UserScreen'
import { VideoScreen } from '../components/Screens/VideoScreen'

export const DashBoardRouters = () => {
  return (
        <>
            <Container />

                <Switch>

                <Route  path="/home" component={ HomeScreen } />
                <Route  path="/photos" component={ PhotosScreen } />
                <Route  path="/tornaments" component={ TornamentScreen } />
                <Route  path="/stores" component={ StoreScreen } />
                <Route  path="/videos" component={ VideoScreen } />
                <Route  path="/user" component={ UserScreen } />
                <Redirect to="/login"  />

                </Switch>

        </>
  )
}
